
import BoardPresenter from './presenter/board-presenter.js';
import FilterPresenter from './presenter/filter-presenter.js';
import PointsModel from './model/point-model.js';
import FilterModel from './model/filter-model.js';

const siteMainElement = document.querySelector('.page-body');
const siteTripInfoElement = siteMainElement.querySelector('.trip-main'); // 
const filtersContainer = siteTripInfoElement.querySelector('.trip-controls__filters'); //  EVERYTHING, FUTURE, PAST  .
const siteEventsElement = siteMainElement.querySelector('.trip-events'); //  DAY, EVENT, TIME .,     
const filterModel = new FilterModel();
const pointsModel = new PointsModel(); //    


const boardPresenter = new BoardPresenter({
  container: siteEventsElement,
  pointsModel,
  filterModel
});


const filterPresenter = new FilterPresenter({ //!!!!!!!!!!!!!!!!
  filterContainer: filtersContainer,
  filterModel: filterModel,
  pointsModel: pointsModel
});

//render(new FilterView({filters}), filtersContainer);

boardPresenter.init();
filterPresenter.init();

